import { Injectable } from '@angular/core';

import { map, first } from 'rxjs/operators';
import { environment } from '../environments/environment';
import { BehaviorSubject, Observable, throwError, of } from 'rxjs';
import { HttpResponse } from '@angular/common/http';

// array in local storage for registered users
const usersKey = 'MobiKart Registered Users';
let users = JSON.parse(localStorage.getItem(usersKey)) || [];

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  public isSession: boolean = false;
  constructor() {}

  login(user: string, pass: string) {
    return this.authenticate(user, pass).pipe(
      map(resp => {
        // login successful if there's a jwt token in the response
        if (resp.body && resp.body.token) {
          // store user details and jwt token in local storage to keep user logged in between page refreshes
          localStorage.setItem('currentUser', JSON.stringify(resp.body));
          return resp;
        }
      })
    );
  }
  /**
   * Method will search given user details in Local Storage
   * @param us
   * @param ps
   */
  authenticate(us, ps) {
    const userStored = users.find(x => x.username === us && x.password === ps);
    if (!userStored) return this.error('Username or password is incorrect');
    this.isSession = true;
    let body = { id: userStored.id, username: userStored.username, firstName: userStored.first, lastName: userStored.last, token: 'fake-token' };
    return of(new HttpResponse({ status: 200, body }));
  }

  /**
   * Method will search given user details in Local Storage
 
   */
  register(regObj) {
    const user = regObj;
    if (users.find(x => x.username === user.username)) {
      return this.error('Username "' + user.username + '" is already taken');
    }
    user.id = users.length ? Math.max(...users.map(x => x.id)) + 1 : 1;
    users.push(user);
    localStorage.setItem(usersKey, JSON.stringify(users));
    return of(new HttpResponse({ status: 200 }));
  }

  logout() {
    // remove user from local storage to log user out
    localStorage.removeItem('currentUser');
    this.isSession = false;
    //this.currentUserSubject.next(null);
  }
  error(message) {
    return throwError({ error: { message } });
  }
}
