import { AuthService } from './../../../../auth.service';
import { MatDialogRef } from '@angular/material/dialog';
import { SnackBarService } from './../../../../snack-bar.service';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { first } from 'rxjs/operators';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  login: boolean = true;
  loginForm: FormGroup;
  registrationForm: FormGroup;

  constructor(private fb: FormBuilder, public loginDialogRef: MatDialogRef<LoginComponent>, public auth: AuthService, public snack: SnackBarService) {}

  ngOnInit(): void {
    this.loginForm = this.fb.group({
      username: ['', Validators.required],
      password: ['', Validators.required]
    });
    this.registrationForm = this.fb.group({
      first: ['', Validators.required],
      last: ['', Validators.required],
      username: ['', Validators.required],
      password: ['', Validators.required]
    });
  }
  //Accessing Form cotrolfields :shothand
  get formLogin() {
    return this.loginForm.controls;
  }
  //Accessing Form cotrolfield:shorthand
  get formReg() {
    return this.registrationForm.controls;
  }
  loginUser() {
    console.log(this.loginForm);
    this.auth
      .login(this.formLogin.username.value, this.formLogin.password.value)
      .pipe(first())
      .subscribe(
        data => {
          this.snack.showSnackBar(`User ${data.body.username} loggedin successfully`);
          this.loginDialogRef.close();

          // this.router.navigate([this.returnUrl]);
        },
        error => {
          console.log('Error', error.error.message);
          this.snack.showSnackBar(error.error.message);
          // this.loading = false;
        }
      );
  }
  registerUser() {
    console.log(this.registrationForm);
    this.auth.register(this.registrationForm.value).subscribe(
      data => {
        console.log('Success', data);
        this.snack.showSnackBar('Registration successfull');
        this.login = true;
      },
      error => {
        console.log('Error', error.error.message);
        this.snack.showSnackBar(error.error.message);
      }
    );
  }
}
