import { SnackBarService } from './../../../../snack-bar.service';
import { AuthService } from './../../../../auth.service';
import { LoginComponent } from './../login/login.component';
import { NavigationService } from './../../navigation.service';
import { Component, OnInit } from '@angular/core';
import { AppServiceService } from 'src/app/app-service.service';
import { MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.scss']
})
export class SearchComponent implements OnInit {
  iptSearch: string;

  constructor(
    public appServ: AppServiceService,
    public authServ: AuthService,
    public navServ: NavigationService,
    public dialog: MatDialog,
    public snack: SnackBarService
  ) {
    this.authServ.isSession = localStorage.getItem('currentUser') ? true : false;
  }

  ngOnInit(): void {}

  searchItem() {
    this.navServ.searchText = this.iptSearch;
  }
  loginPage() {
    this.dialog.open(LoginComponent, {
      height: '528px',
      width: '650px',
      panelClass: 'loginPanel'
    });
  }
  logout() {
    this.authServ.logout();
    this.snack.showSnackBar('Logout successfull');
  }
}
